@import "https://fonts.googleapis.com/css2?family=Inter:wght@500&family=Montserrat:wght@400;500;600;700;800;900&family=Seymour+One&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

* {
  box-sizing: border-box;
}

ul {
  list-style-type: none;
}

ul, li, p {
  margin: 0;
  padding: 0;
}

a {
  cursor: pointer;
  color: inherit;
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
}

body {
  color: #131313;
  background-image: url("background.e9fd6ef4.jpg");
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
}

.container {
  max-width: 1168px;
  margin: 0 auto;
}

p, h1, h2, h3 {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a, address {
  color: #131313;
  font-style: normal;
  text-decoration: none;
}

.header {
  background-color: #84a178;
}

.header-flex__container {
  justify-content: space-between;
  align-items: center;
  padding: 12px 20px 0;
  display: flex;
}

.site-logo {
  fill: #f8d24e;
}

.site-icon-logo {
  width: 40px;
  height: 37px;
}

.site-nav {
  width: 444px;
  display: none;
}

.site-nav__list {
  max-width: 100%;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.site-nav__link {
  color: #fcfcfc;
  padding: 10px 0;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.25;
  text-decoration: none;
  display: inline-block;
}

.site-nav__link:hover, .site-nav__link:focus {
  color: #f8d24e;
}

.menu-toggle--open {
  margin-bottom: 5px;
  margin-left: auto;
}

.button__buy {
  width: 152px;
  height: 42px;
  cursor: pointer;
  color: #000;
  background-color: #f8d24e;
  border: none;
  border-radius: 10px;
  padding: 0;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
}

.site-button__buy {
  margin-bottom: 5px;
  display: none;
}

.site-button__buy.buy-arrow-in-button {
  margin-left: 4px;
}

@media screen and (min-width: 768px) {
  .header-flex__container {
    padding: 20px 32px 0;
  }

  .site-icon-logo {
    width: 48px;
    height: 44px;
  }

  .menu-toggle--open {
    margin-right: 32px;
  }

  .site-button__buy {
    display: block;
  }
}

@media screen and (min-width: 800px) {
  .site-nav {
    display: block;
  }

  .menu-toggle--open {
    display: none;
  }
}

@media screen and (min-width: 1200px) {
  .header-flex__container {
    padding: 8px 16px 0;
  }

  .site-icon-logo {
    width: 60px;
    height: 65px;
  }
}

.about {
  color: #fcfcfc;
  text-align: center;
  background-color: #84a178;
  padding-bottom: 99px;
  padding-left: 16px;
  padding-right: 16px;
  font-weight: 400;
}

.about__label {
  text-transform: uppercase;
  margin-bottom: 12px;
  padding-top: 60px;
  font-size: 30px;
  font-weight: 800;
  line-height: 1.2;
}

.about__heading {
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 82px;
  font-family: Seymour One, sans-serif;
  font-size: 36px;
  line-height: 45px;
}

.about-card {
  justify-content: center;
  align-items: center;
  gap: 110px;
  display: flex;
}

.about-card__img-wrapper {
  width: 490px;
  height: 490px;
  background-color: #f8d24e;
  border-radius: 50%;
  flex: none;
  position: relative;
}

.about-card__img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 39px;
  left: 1px;
}

.about-card__info {
  text-align: left;
  padding-right: 111px;
}

.about-card__text {
  letter-spacing: .04em;
  margin-bottom: 8px;
  font-size: 14px;
  line-height: 194%;
}

.about-card__text--first {
  font-size: 16px;
  line-height: 176%;
}

.about-card__read-more {
  cursor: pointer;
  background-color: #f8d24e;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  margin-top: 46px;
  padding: 12px 34px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.3;
}

@media screen and (max-width: 1199px) {
  .about {
    padding-bottom: 86px;
    padding-left: 32px;
    padding-right: 32px;
  }

  .about__label {
    font-size: 22px;
  }

  .about__heading {
    margin-bottom: 72px;
    font-size: 34px;
  }

  .about-card {
    align-items: flex-start;
    gap: 38px;
  }

  .about-card__img-wrapper {
    width: 330px;
    height: 330px;
  }

  .about-card__img {
    top: 26px;
    left: 0;
  }

  .about-card__info {
    max-width: 500px;
    text-align: left;
    padding-right: 2px;
  }

  .about-card__text {
    font-size: 12px;
  }

  .about-card__text--second {
    line-height: 176%;
  }

  .about-card__read-more {
    margin-top: 24px;
  }
}

@media screen and (max-width: 767px) {
  .about {
    padding-bottom: 50px;
    padding-left: 20px;
    padding-right: 20px;
  }

  .about__label {
    font-size: 18px;
    font-weight: 700;
  }

  .about__heading {
    letter-spacing: .04em;
    max-width: 320px;
    margin: 12px auto 32px;
    font-size: 30px;
  }

  .about-card {
    flex-direction: column;
    align-items: center;
    gap: 62px;
  }

  .about-card__img-wrapper {
    width: 279px;
    height: 278px;
  }

  .about-card__img {
    top: 22px;
    left: 1px;
  }

  .about-card__info {
    max-width: 320px;
    text-align: center;
    padding-left: 30px;
    padding-right: 30px;
  }

  .about-card__text {
    margin-top: 4px;
    font-size: 12px;
  }

  .about-card__text--first {
    margin: 0;
  }

  .about-card__text--second {
    line-height: 162%;
  }

  .about-card__text--third {
    display: none;
  }

  .about-card__read-more {
    margin-top: 2px;
  }
}

.feedback {
  max-width: 1013px;
  background-repeat: no-repeat;
  background-size: cover;
  margin: auto;
  padding-top: 0;
  padding-bottom: 393px;
}

.feedback__container {
  padding-left: 20px;
  padding-right: 20px;
}

.feedback__image-block {
  margin: 0 auto;
  display: block;
  top: -32px;
}

.feedback__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
  transform: translate(0, -32px);
}

.feedback__item {
  flex-direction: column;
  align-items: center;
  margin-top: 58px;
  display: flex;
}

.feedback__avatar {
  width: 85px;
  height: 85px;
}

.feedback-block {
  background: #84a178;
  border-radius: 10px;
  margin-top: 32px;
  padding: 35px 39px;
}

.feedback-block__text {
  letter-spacing: .04em;
  text-align: center;
  color: #fcfcfc;
  font-size: 12px;
  line-height: 2.58333;
}

.feedback__subtext {
  text-align: center;
  color: #131313;
  margin-top: 32px;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.85;
}

.feedback__switch {
  margin-top: 12px;
  display: flex;
}

.feedback__button {
  width: 12px;
  height: 12px;
  background: #d9d9d9;
  border-radius: 50%;
}

.feedback__button:not(:last-child) {
  margin-right: 18px;
}

.feedback__button--active {
  background: #84a178;
}

@media screen and (min-width: 768px) {
  .feedback {
    padding-bottom: 379px;
  }

  .feedback__image-block {
    max-width: 900px;
    transform: translate(0, -32px);
  }

  .feedback__img {
    border-radius: 18px;
  }

  .feedback__avatar {
    width: 84px;
    height: 84px;
  }

  .feedback-block {
    border-radius: 24px;
    padding: 28px 80px;
  }

  .feedback-block__text {
    font-size: 14px;
    line-height: 1.85714;
  }

  .feedback__subtext {
    font-size: 16px;
    line-height: 1.875;
  }

  .feedback__switch {
    margin-top: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .feedback {
    padding-bottom: 228px;
  }

  .feedback__image-block {
    max-width: 1013px;
  }

  .feedback__img {
    border-radius: 24px;
    transform: translate(0, -32px);
  }

  .feedback__avatar {
    width: 85px;
    height: 85px;
  }

  .feedback-block {
    padding: 43px 109px;
  }

  .feedback-block__text {
    font-size: 16px;
    line-height: 1.875;
  }
}

.products {
  text-align: center;
}

.products__label {
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 800;
  line-height: 22px;
}

.products__title {
  letter-spacing: .04em;
  text-transform: uppercase;
  margin-bottom: 148px;
  font-family: Seymour One, sans-serif;
  font-size: 30px;
  font-weight: 400;
  line-height: 38px;
}

.product__title {
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  padding-bottom: 40px;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
}

.product__description {
  text-align: center;
  letter-spacing: .04em;
  padding-bottom: 109px;
  font-size: 14px;
  line-height: 176%;
}

.product__card {
  text-align: center;
  border-radius: 24px;
  margin-bottom: 168px;
  margin-left: 20px;
  margin-right: 20px;
  padding-top: 123px;
  padding-left: 40px;
  padding-right: 40px;
  position: relative;
}

.product__card--ice-cream {
  background-color: #aec6a5;
}

.product__card--ice-coffee {
  background-color: #f8d24e;
}

.product__card--milkshake {
  background-color: #d6936d;
}

.product__card:before {
  content: "";
  width: 200px;
  height: 200px;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
  display: block;
  position: absolute;
  top: -25%;
  left: 0;
  right: 0;
  box-shadow: 0 5.41333px 20.3px #00000040;
}

.product__card--milkshake:before {
  background-color: #d6936d;
  background-image: url("product-milkshake.b2a73c71.png");
  background-position: 0 -12px;
  background-repeat: no-repeat;
  background-size: contain;
}

.product__card--ice-cream:before {
  background-color: #aec6a5;
  background-image: url("product-icecream.09db1746.png");
  background-position: 0 -12px;
  background-repeat: no-repeat;
  background-size: contain;
}

.product__card--ice-coffee:before {
  background-color: #f8d24e;
  background-image: url("product-coffee.80e9493f.png");
  background-position: 0 -12px;
  background-repeat: no-repeat;
  background-size: contain;
}

.product__link {
  margin-left: auto;
  margin-right: auto;
  display: inline-block;
  position: absolute;
  bottom: 24px;
  left: 0;
  right: 0;
}

.products__flex {
  flex-wrap: wrap;
  justify-content: center;
  padding-left: 24px;
  padding-right: 24px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .products__flex {
    flex-wrap: nowrap;
  }
}

@media screen and (min-width: 480px) {
  .products {
    background-image: url("background-mob.57a7e544.jpg");
    padding-top: 61px;
    padding-bottom: 60px;
  }
}

@media screen and (max-width: 768px) {
  .products__card {
    max-width: 280px;
  }
}

@media screen and (min-width: 768px) {
  .products {
    background-image: url("background-tablet.d96319b8.jpg");
  }
}

@media screen and (min-width: 768px) {
  .product__card {
    flex-basis: calc(33.3333% - 16px);
    margin: 8px;
  }
}

@media screen and (min-width: 768px) {
  .product__card:before {
    top: -20%;
  }
}

@media screen and (min-width: 1200px) {
  .products {
    background-image: url("background.e9fd6ef4.jpg");
  }
}

@media screen and (min-width: 1200px) {
  .product__card:before {
    width: 302px;
    height: 302px;
  }
}

@media screen and (min-width: 1200px) {
  .product__card {
    padding-top: 165px;
    padding-left: 64px;
    padding-right: 64px;
  }
}

@media screen and (min-width: 1200px) {
  .product__card:before {
    top: -151px;
  }
}

@media screen and (min-width: 1200px) {
  .products__title {
    margin-bottom: 302px;
  }
}

@media screen and (min-width: 1200px) {
  .product__card--ice-coffee:before, .product__card--ice-cream:before, .product__card--milkshake:before {
    background-position: 0 -16px;
  }
}

@media screen and (min-width: 768px) {
  .products__title {
    font-size: 34px;
    line-height: 43px;
  }
}

@media screen and (min-width: 768px) {
  .products__label {
    font-size: 22px;
    line-height: 27px;
  }
}

@media screen and (max-width: 767px) {
  .product__description {
    max-width: 200px;
  }
}

@media screen and (min-width: 1200px) {
  .product__description {
    min-width: 242px;
    padding-bottom: 126px;
    font-size: 16px;
    line-height: 28px;
  }
}

@media screen and (min-width: 1200px) {
  .products__title {
    letter-spacing: .06em;
    font-size: 36px;
    line-height: 45px;
  }
}

@media screen and (min-width: 1200px) {
  .products__label, .product__title {
    font-size: 30px;
    line-height: 37px;
  }
}

@media screen and (min-width: 1200px) {
  .product__link {
    bottom: 67px;
  }
}

.menu-container {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  text-align: center;
  background-color: #84a178;
  flex-direction: column;
  justify-content: space-around;
  padding: 20px;
  transition: all .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateY(-100%);
}

.menu-container.is-open {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.mobile-logo {
  fill: #f8d24e;
  position: absolute;
  top: 12px;
  left: 32px;
}

.mobile-icon-logo {
  width: 40px;
  height: 37px;
}

.menu-toggle {
  fill: #fff;
  cursor: pointer;
  background-color: #0000;
  border: none;
  padding: 0;
}

.menu-toggle--close {
  position: absolute;
  top: 16px;
  right: 20px;
}

.mobile-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.mobile-nav__item:not(:last-child) {
  margin-bottom: 32px;
}

.mobile-nav__link {
  color: #fcfcfc;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.22;
  text-decoration: none;
}

.mobile-nav__link:hover, .mobile-nav__link:focus {
  color: #131313;
}

.mobile-button__buy {
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .menu-container {
    background-color: #97af8d;
    padding: 32px;
  }

  .mobile-logo {
    top: 20px;
  }

  .mobile-icon-logo {
    width: 48px;
    height: 44px;
  }

  .menu-toggle--close {
    top: 32px;
    right: 32px;
  }

  .mobile-nav__item:not(:last-child) {
    margin-bottom: 40px;
  }

  .mobile-nav__link {
    font-size: 48px;
    line-height: 1.23;
  }

  .mobile-button__buy {
    display: none;
  }
}

@media screen and (min-width: 1200px) {
  .menu-container {
    display: none;
  }
}

